@import "styles/variables.scss";

.navbarContainer {
    position: absolute;
    top: 60px;
    left: 0;
    width: 100vw;
    height: 240px;
    padding: 40px;
    z-index: 100;
    background-color: $beige;
    border-bottom: 1px solid rgba(35, 35, 35, 0.3);
}

.navbarContainerBlock {
    display: flex;
    width: 100%;
    height: 100%;
}

.navbarContainerBlock > * {
    font-style: normal;
    font-weight: 450;
    font-size: 16px;
}

.navbarContainerBlock > div:not(:last-child) {
    margin-right: 0;
}

.categories {
    display: flex;
    flex-direction: column;
}

.categories > a:not(:last-child) {
    margin-bottom: 15px;
}

.categories > a {
    text-transform: uppercase;
    text-decoration: none;
}

.section {
    display: flex;
    justify-content: space-between;
}

.sectionContainer {
    display: flex;
    flex-direction: column;
}

.sectionTitle {
    text-transform: uppercase;
    margin-bottom: 15px;
    text-decoration: none;
}

.sectionCol {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    row-gap: 8px;
    column-gap: 2px;
    height: 85%;
}

.navbarContainer .sectionItem {
    font-family: "IBM Plex Mono", "20Kopeek-Book", sans-serif;
    text-decoration: none;
}

.lkAndTranslate {
    display: none;
    justify-content: space-between;
    padding: 60px 0 100px;
    width: calc(100% - 48px);
}

.lkAndTranslate > a > button {
    font-size: 16px;
    text-transform: uppercase;
}

.lkAndTranslate > a:last-child > button {
    text-decoration: underline;
}

.isBunnerMove {
    position: absolute;
    right: 120px;
}

@media screen and (min-width: 1681px) {
    .navbarContainerBlock {
        padding: 0 40px;
    }
    .navbarContainer {
        padding: 40px 0;
        display: flex;
        justify-content: center;
    }
}

@media #{$bigtablet} {
    .isBunnerMove {
        display: none;
    }
}

@media #{$mobile} {
    .lkAndTranslate {
        display: flex;
    }

    .navbarContainer {
        padding-top: 0;
        top: 142px;
        padding-top: 24px;
        height: calc(100vh - 205px);
        padding-left: 0;
        overflow-y:scroll;
        overflow-x:hidden;
    }

    .navbarContainerBlock {
        flex-direction: column;
    }
    .section {
        flex-direction: column;
    }
    .categories > a:not(:last-child) {
        margin-bottom: 24px;
    }
    .navbarContainerBlock > div:not(:last-child) {
        margin: 0 0 24px 24px;
        width: calc(100% - 48px);
    }
    .navbarContainerBlock > div:last-child {
        margin: 0 0 24px 24px;
    }
    .sectionCol {
        margin: 0 0 24px 0;
        height: auto;
    }
    .sectionCol > a {
        margin-left: 36px;
    }
    .navbarContainer .sectionItem,
    .sectionCol,
    .categories > a {
        font-size: 16px;
        width: auto!important;
    }
    .sectionCol {
        width: auto!important;
    }
    .sectionTitle {
        font-size: 16px;
    }
}
